/* input styling start */
.inputBox{
    bottom: 0;
    position: fixed;
    width: 100vw;
}
.input-box{
    position: relative;
    display: flex;
    background-color: #0D120E;
    padding:  3px 0px;
    height: 50px;
    align-items: center;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
}
.img-insert{
    width: 41px;
    height: 29px;
    padding: 5px 0;
    cursor: pointer;
    margin: 0 5px;
}
.img-insert:hover{
    background-color: white;
    border-radius: 30%;
}
.img-img{
    width: 25px;
    height: 25px;
    cursor: pointer;
    padding: 5px;
    
}
.img-img:hover{
    background-color: white;
    border-radius: 30%;
    
}
.type-something{
    margin: 0px 0px 0 10px; 
    font-size: 16px; 
    width: 80vw;
    padding: 12px 10px;
    border-radius: 20px;
    text-align: left;
}
.send{
    display: flex;
    align-items: center;
}
.send-btn{
    font-size: 19px;
    border: none;
    background: none;
    padding: 10px;
    cursor: pointer;
}
.send-img{
    width: 35px;
    height: 35px;
}
.send-img:hover{
    background-color: white;
    border-radius: 30%;
}
.repliedMsgBox{
    background-color: #131513;
    padding: 10px 25px;
    color: white;
    display: none;
}