.sidebar-box{
    background-color: #232826;
    width: 100vw;
    border-radius: 10px;
    /* display: none; */
    overflow-y: scroll;
    /* margin-top: 10px; */
    }
/* navbar styling start    */
.navbar-box{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-content: center;
    align-items: center;
    height: 50px;
    width: 100vw;
    background-color: #0D120E;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    position: fixed;
    top: 0;
}
.line{
    content: "";
    height: 2px;
    width: 20px;
    background-color: black;
    margin: 2px 10px;
}
.App-Name{
    position: relative;
    text-align: center;
    color: #96a9a2;
    padding: 0px 8px;
    top: -4px;
}

 /* for search.js start */
 .search{
    position: relative;
    position: fixed;
    top: 50px;
    width: 100vw;
    border-bottom: 1px solid rgba(128, 128, 128, 0.253);
 }
 .searchForm{
    display: grid;
    grid-template-columns: 9fr 1fr;
    background-color: #232826;
 }
.search-input{
    background-color: #232826;
    /* color: white; */
    padding: 15px 15px  15px 0;
}
.userChat{
    display: flex;
    padding: 10px 10px 10px 0;
    border-bottom: 1px solid rgba(128, 128, 128, 0.253);
}
.userChat:hover{
    background-color: #373f3c;
    cursor: pointer;
    
}
.search-img{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-left: 10px;
    background-color: #232826;
    border: 2px solid #0D120E;
    padding: 2px;
}
.userChatInfo{
    display: flex;
    align-items: center;
    font-size: 20px;
    margin-left: 20px;
    color: #96a9a2;
}
 /* chats styling start */
 .chats{
   margin-top: 105px;
 }
 .chatsDisplay{
    margin-top: 180px;
  }
 .userChatInfoChats{
    margin-left: 20px;
    padding-right: 58vw;
}
.chats-name{
    font-size: 18px;
    color: #96a9a2;
    font-weight: 600;
}

.latestMsg{
    font-size: 16px;
}
/* chat stylig start */
.backarrow{
    width: 20px;
    height: 25px;
    position: relative;
    top:7.5px;
    color: #96a9a2;
    font-size: 24px;
    left: 15px;
    margin-right: 12px;
    cursor: pointer;
}

 .typing{
    font-size: 12px;
    position:fixed;
    top: 40px;
    margin-left: 85px;
    color: #96a9a2;

 }
.chat-user-icon{
    position: relative;
    margin-left: 20px;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    top: 15px;
}
.logout-btn{
    position: relative;
    display: flex;
    background: none;
    margin-right: 20px;
    float: right;
    align-items: center;
    border: none;
    color: #96a9a2;
    cursor: pointer;
}
.chatInfo{
    display: grid;
    background-color: #0D120E;
    grid-template-columns: 1fr 1fr;
    height: 60px;
    align-items: center;
    width: 100vw;
    position: fixed;
    top: 0;
    border-radius: 10px 10px 0 0;
}
.person-name{
    position: relative;
    font-size: 18px;
    top: -18px;
    left: 85px;
    color: #96a9a2;
}
.main{
    overflow: none;
}

/* message styling start */
.Messages-box{
    position: relative;
    margin-top:136px;
    padding-top: 45px;
    bottom: 76px;
    overflow-y: scroll;
    overflow-x: hidden;
    width: 100vw;
    height: calc(100vh - 156px);
}
.messages-box{
    position: relative;
    top: 20px;
    display:inline-table;
    padding: 15px 0 0 15px;
}
.message{
    margin: -82px 0;
    padding: 20px 0;
    
}
.forword{
    width: 15px;
    height: 15px;
    margin-left: 3px;
}
.messageInfo{
    
    position: relative;
    top: -7px;
    right: -5px;
}
.message-user-icon{
    width: 30px;
    height: 30px;
    border-radius:50%;
}
.massageContent{
    position: relative;
    top: -55px;
    left: 50px;
    display: flex;
    max-width: 56vw;
    overflow-x: scroll;
    align-items: center;
}
.textMessage{
    display: grid;
    grid-template-columns: 1fr ;
    border-radius: 0 10px 10px 10px;
    background-color: #434645;
    color: rgb(226, 226, 226);
    padding: 4px 8px 8px 8px;
    margin: 0 2px ;
}

.ImageContent{
    position: relative;
    top: -58px;
    background-color: #0D120E;
    max-width: 100%;
    margin-top: 5px;
    right: -55px;
    border-radius: 0 5px 5px 5px;
}
.message-img{
    margin-left: 6px;
    margin-right: 6px;
    width: 55vw;
    max-width: 300px;
    cursor: pointer;
    margin-bottom:-5px;
    /* filter: blur(8px); */
    /* width: 100vw; */
}
.justnow{
    position: relative;
    font-size: 12px;
    color: white;
}
/* for owner side messages */
.owner{
    transform: scale(-1,1);
    border-radius: 10px 0px 10px 10px;
}
.owner1{
    background-color: #111514;
}
.display{
    display: block;
}
.display1{
    display: none;
}
.display5{
    display: flex !important;
    box-shadow: 10px 10px 58px 50px rgb(37, 39, 39);
}

.display3{
    box-shadow: 10px 10px 58px 300px rgb(37, 39, 39);
    position: absolute;
    width:92.5vw;
    height: 92.5vw;
    object-fit: cover;
    top: 26vh;
    border-radius: 0 !important;
    transition-duration: 0.4s;
    z-index: 2;
}

/* userInfo page styling */
.display4{
    width: 95vw !important;
    height:50vh !important;
    position: relative;
    top: 15vh;
    transition-duration: 0.4s;

}
.userDetails{
    height: 100vh;
    width: 100vw;
    overflow: hidden;
}
.userInfoDp{
    width: 100vw !important;
    display: flex;
    justify-content: center;
    border-radius: 50%;
    height: 120px;
    width: 120px;
    padding-top: 10vh;
}
.Details{
    margin: 10px 20px;
}
.userDp{
    height: 120px;
    width: 120px;
    border-radius: 50%;
}
.userInfoName{
    background-color:#0D120E;
    padding: 10px 10px;
    display: flex;
    color:#96a9a2;
    margin-top: 5px;
    margin-bottom: 20px;
}
.user-name-label{
    color:#96a9a2;
    margin-left: 10px;
}
.userInfoEmail{
    background-color:#0D120E;
    padding: 10px 10px;
    display: flex;
    color:#96a9a2;
    margin-top: 5px;

}
