*{
    margin: 0;
    padding: 0;
}
.inputs{
    position: relative;
    margin: 25vh 20px;
    padding: 40px 10px;
    border-radius: 10px;
    background-color: #0D120E;
}
body{
    background-color: #232826;
}
.type{
    position: absolute;
    z-index: 1;
    top: calc(25vh + 10px);
    left: calc(50% - 39.125px);
    font-size: 22px;
    color: #96a9a2;
}
.type-login{
    position: absolute;
    z-index: 1;
    top: calc(25vh + 10px);
    left: calc(50% - 26.995px);
    font-size: 22px;
    color: #96a9a2;
}
.label{
    font-size: 19px;
    color: #96a9a2;
    margin: 2px 20px;
}
input{
  display: flex;
  grid-template-columns: 1fr;
  padding: 5px 10px;
  border-radius: 5px;
  outline: none;
  border: none;
  margin: 5px 20px;
  color: #96a9a2;
  background-color: #3C413F;
}
.input{
    display: grid;
}
.add-profile-label{
    font-size: 14px;
}
.submit{
    padding: 8px 45px;
    margin: 0px 20px;
    border-radius: 5px;
    font-size: 18px;
    border: none;
    background-color: #1DA756;
    color: white;
    margin-top: 10px;
    cursor: pointer;
}
.HaveOrNotAccount{
    position: relative;
    float: right;
    right: 20px;
    top: 10px;
    text-decoration: none;
    color: #96a9a2;
}
.HaveOrNotAccount:hover{
    text-decoration: underline;
    color: #d7d8d8;
}
/* homepage */

.homeContainer{
    position: relative;
    margin: 0;
    height: 100vh;
}
.innerBox{
    /* border: 1px solid red; */
    width: 100vw;
    height: 100vh;
    display: flex;
    border-radius: 10px;
}
